@import url(https://fonts.googleapis.com/css2?family=Kranky&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rammetto+One&display=swap);
*{margin:0;padding:0;box-sizing:border-box}
*{font-family:"Kranky",cursive}
.mainPage{background-color:#c43b5e;color:#d8dee9}.mainPage .mainPageTitle{display:flex;align-items:center;justify-content:center;font-size:5.5vmin;padding:3vmin}.mainPage .templates{margin:1vmin;border-radius:5vmin}.mainPage .templates .template{width:33.33%;display:inline-block;padding:3vmin}.mainPage .templates .template .image{cursor:pointer;width:100%;height:500px;background-position:center;background-size:cover;border-radius:4vmin;transition:all .2s ease-in-out;border:.1vmin solid #d8dee9}.mainPage .templates .template .image:hover{border:.5vmin solid #d8dee9;cursor:pointer;width:100%;height:500px;background-position:center;background-size:cover;border-radius:4vmin;-webkit-transform:scale(1.1);transform:scale(1.1)}
.memePage{background-color:#c43b5e;color:#d8dee9;height:100vh;display:grid;place-content:center}.memePage .container{width:50vw;padding:3vmin;border-radius:1vmin;border:1vmin solid #4c566a;align-items:center;justify-content:center;display:flex;flex-flow:column;background-color:#3bc4a1}.memePage .container .memePageImg img{border-radius:2vmin;border:.8vmin solid #d8dee9;height:500px}.memePage .container .memePageInp{border-radius:1vmin;margin:1vmin;display:grid;grid-template-columns:auto auto}.memePage .container .memePageInp input{padding:1vmin;border-radius:1vmin;margin:1vmin}.memePage .container .memePageBtns{display:flex;justify-content:space-evenly;margin:2vmin;font-size:2.5vmin;font-weight:800}.memePage .container .memePageBtns .goBack{background-color:#c43b5e;margin:1vmin;color:#d8dee9;padding:2vmin;border-radius:1vmin;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.memePage .container .memePageBtns .generateMeme{background-color:#c43b5e;margin:1vmin;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;padding:2vmin;border-radius:1vmin;color:#d8dee9}.memePage .container .memePageBtns .download{background-color:#c43b5e;margin:1vmin;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;padding:2vmin;border-radius:1vmin;color:#d8dee9}
.homePage{font-family:"Rammetto One",cursive;background-color:#c43b5e;color:#d8dee9;height:100vh;display:flex;flex-flow:column;justify-content:center;align-items:center}.homePage .homePageTitle{display:flex;flex-flow:column;justify-content:center;align-items:center;font-size:5vmin}.homePage .homePageBtn .playBtn{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;font-size:3vmin;background-color:#000;padding:2vmin 4vmin 2vmin 4vmin;border-radius:1vmin;border:.3vmin solid #add8e6;transition:all .2s ease-in-out;margin:3vmin}.homePage .homePageBtn .playBtn:hover{-webkit-user-select:none;-ms-user-select:none;user-select:none;border:.3vmin solid #add8e6;cursor:pointer;font-size:3vmin;background-color:#000;padding:2vmin 4vmin 2vmin 4vmin;border-radius:1vmin;-webkit-transform:scale(1.3);transform:scale(1.3);margin:25vmin}
